<template>
    <div style="padding-top: 5px;">
        <el-form :inline="true" ref="searchFormRef" :model="searchFormData" size="mini">
            <el-form-item label="项目组名称" prop="project_team_id">
                <el-select v-model="searchFormData.project_team_id"
                           placeholder="请选择项目组"
                           clearable
                           @change="project_team_change"
                >
                    <el-option v-for="item in project_team_code_list"
                               :key="item._id"
                               :label="item.project_team_name"
                               :value="item._id">
                    </el-option>
                </el-select>
            </el-form-item>

            <el-form-item label="项目名称" prop="project_id">
                <el-select v-model="searchFormData.project_id"
                           placeholder="请选择项目"
                           clearable
                >
                    <el-option v-for="item in project_selected_code_list"
                               :key="item._id"
                               :label="item.project_name"
                               :value="item._id">
                    </el-option>
                </el-select>
            </el-form-item>

            <el-form-item label="操作时间" prop="createTime">
                <el-date-picker
                        v-model="searchFormData.createTime"
                        type="daterange"
                        range-separator="至"
                        start-placeholder="开始时间"
                        end-placeholder="结束时间">
                </el-date-picker>
            </el-form-item>

            <el-form-item label="操作类型" prop="project_opt_type_code_id">
                <el-select v-model="searchFormData.project_opt_type_code_id" placeholder="操作类型" clearable>
                    <el-option v-for="item in project_opt_type_code_list"
                               :key="item.codeId"
                               :label="item.codeValue"
                               :value="item.codeId">
                    </el-option>
                </el-select>
            </el-form-item>
            <div class="v-search-opt">
                <div class="v-pagination">
                    <el-pagination
                            @size-change="handleSizeChange"
                            @current-change="handleCurrentChange"
                            :current-page="searchFormData.pageNum"
                            :page-size="searchFormData.pageSize"
                            :total="searchFormData.pageCount"
                            :page-sizes="[18, 50, 100]"
                            layout="total, sizes, prev, pager, next, jumper">
                    </el-pagination>
                </div>
                <div class="v-inline">
                    <el-button size="mini" type="primary" @click="onSearch"><i class="iconfont icon-chaxun"></i> 查询</el-button>
                    <el-button size="mini" type="primary" @click="onReset"><i class="iconfont icon-chongzhi"></i> 重置</el-button>
                </div>
            </div>
        </el-form>
        <hr>
        <el-table :data="tableData" :border="true" size="small" :height="getTableHeight(98)">
            <el-table-column fixed type="index" :index="onIndexMethod(searchFormData.pageNum,searchFormData.pageSize)" label="序号" width="60">
            </el-table-column>
            <el-table-column
                    prop="createTime"
                    label="操作时间"
                    width="156">
            </el-table-column>
            <el-table-column
                    width="156"
                    :show-overflow-tooltip="true"
                    prop="project_id"
                    label="项目名称"
                    :formatter="format_project">
            </el-table-column>
            <el-table-column
                    prop="project_opt_user_name"
                    label="操作人"
                    width="80"
            ></el-table-column>
            <el-table-column
                    prop="project_opt_version"
                    label="操作版本"
                    width="80"
            >
            </el-table-column>
            <el-table-column
                    prop="project_opt_last_version"
                    label="上个版本"
                    width="80"
            >
            </el-table-column>
            <el-table-column
                    prop="project_opt_type_code_id"
                    label="操作类型"
                    width="80"
                    :formatter="format_project_opt_type"
            >
            </el-table-column>
            <el-table-column
                    prop="is_opt_success"
                    label="操作结果"
                    width="80"
                    :formatter="format_is_opt_success"
            >
            </el-table-column>
            <el-table-column
                    prop="is_able_rollback"
                    label="可回滚"
                    width="60"
                    :formatter="format_is_able_rollback"
            >
            </el-table-column>
            <el-table-column
                    prop="project_opt_start_time"
                    label="操作开始时间"
                    width="156">
            </el-table-column>
            <el-table-column
                    prop="project_opt_end_time"
                    label="操作结束时间"
                    width="156">
            </el-table-column>
            <el-table-column
                    prop="exec_state_code_id"
                    label="远程执行状态"
                    :formatter="format_exec_state"
                    width="100"
            >
            </el-table-column>
            <el-table-column
                    prop="remote_exec_count"
                    label="获取状态次数"
                    width="100"
            >
            </el-table-column>
            <el-table-column
                    prop="remote_exec_result"
                    label="远程执行结果"
                    :show-overflow-tooltip="true"
                    :formatter="format_remote_exec_result"
            >
            </el-table-column>
            <el-table-column
                    prop="project_opt_summary"
                    label="操作描述"
                    :show-overflow-tooltip="true"
            >
            </el-table-column>
        </el-table>
    </div>
</template>

<script>
    export default {
        name: 'projectOptRecord',
        data() {
            return {
                project_opt_type_code_list: null,
                exec_state_code_list: null,
                project_code_list: null,
                project_team_code_list: [],
                project_selected_code_list: [],
                msg_level_list: null,
                searchFormData: {
                    pageNum: 1,
                    pageSize: 18,
                    pageCount: 1,
                    order: '-createTime'
                },
                tableData: []
            }
        },
        async mounted() {
            this.project_code_list = await this.$getProjectCodeInfo();
            this.project_opt_type_code_list = await this.$setCodeList(this.project_opt_type_code_list, 'project_opt_type');
            this.exec_state_code_list = await this.$setCodeList(this.exec_state_code_list, 'exec_state');
            this.project_team_code_list = await this.$getProjectTeamCodeInfo();
            this.search();
        },
        methods: {
            async project_team_change(project_team_id)/*项目组选择变更*/ {
                this.project_selected_code_list = await this.$getProjectCodeInfo({'project_team_id': project_team_id});
                if (this.searchFormData.project_id !== undefined) {
                    this.searchFormData.project_id = '';
                }
            },
            handleSizeChange(val) //修改单页显示条数
            {
                this.searchFormData.pageSize = val;
                this.search();
            },
            handleCurrentChange(val) //当前页修改
            {
                this.searchFormData.pageNum = val;
                this.search();
            },
            onReset() // 重置按钮
            {
                this.$refs.searchFormRef.resetFields();
                this.search();
            },
            onSearch() // 查询按钮
            {
                this.searchFormData.pageNum = 1;
                this.search();
            },
            async search() {
                let searchData = this.searchFormData;
                let data = await this.$search(searchData, 'OAMP_search_project_opt_record_info_list');
                this.searchFormData.pageCount = data.pageCount;
                this.tableData = data.data;
            },

            format_exec_state(row, column, cellValue) // 格式化项目操作类型
            {
                return this.$formatCodeValue(row, this.exec_state_code_list, 'exec_state_code_id')
            },
            format_project_opt_type(row, column, cellValue) // 格式化项目操作类型
            {
                return this.$formatCodeValue(row, this.project_opt_type_code_list, 'project_opt_type_code_id')
            },
            format_project(row, column, cellValue) //格式化项目名称
            {
                return this.$formatCodeValue(row, this.project_code_list, column.property, '_id', 'project_name')
            },
            format_is_opt_success(row, column, cellValue) {
                if (row.is_opt_success === '1') {
                    return '成功'
                } else if (row.is_opt_success === '0') {
                    return '失败'
                }
                else
                    return '暂无结果'
            },
            format_is_able_rollback(row, column, cellValue) {
                return row.is_able_rollback === "1" ? '是' : '否';
            },
            format_remote_exec_result(row, column, cellValue) {
                let result = row.remote_exec_result;
                let s = '';
                if (row && result.length > 0) {
                    for (let i = 0; i < result.length; i++) {
                        s = s + '【' + result[i].request_ip + '服务器' + result[i].summary + '】'
                    }
                }
                return s;
            },
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    h1 {
        font-weight: normal;
    }

    .item {
        margin: 4px;
    }

</style>



